*{
    margin:0;
    padding:0;
}
li{
    list-style: none;
}
.ant-pagination-item{
  position:relative;
  left:50px;
  top:-16px;
}
.ant-pagination-next{
  position:relative;
  left:100px;
  top:-36px;
}
#box{margin:0 auto}
#customers
  {
  font-family:"Trebuchet MS", Arial, Helvetica, sans-serif;
  width:100%;
  border-collapse:collapse;
  }
 
#customers td, #customers th 
  {
  font-size:1em;
  border:1px solid #98bf21;
  padding:3px 7px 2px 7px;
  }
 
#customers th 
  {
  font-size:1.1em;
  text-align:left;
  padding-top:5px;
  padding-bottom:4px;
  background-color:#A7C942;
  color:#ffffff;
  }
 
#customers tr.alt td 
  {
  color:#000000;
  background-color:#EAF2D3;
  }
