*{
    padding: 0;
    margin: 0;
}
a{
    text-decoration: none;
}
header{
    height: 80px;
    line-height: 48px;
    background-color: #20232a;
}
.houtai{
    width: 40px;
    height: 40px;
    position: relative;
    top: 15px;
    left: 40px;
}
nav{
    width: 100%;
    /* margin: 0 auto; */
    position: relative;
    /* left: 50px; */
    top: 20px;
    font-size: 20px;
}
header a{
    display: inline-block;
    height: 48px;
    padding: 0 15px;
    color: #fff;
    margin-left: 30px;
}
header .active{
    border-bottom: 2px solid rgb(97, 218, 251);
    color: rgb(97, 218, 251);
}
.title{
    height: 700px;
    background-image: url(./bg.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}
